import { Wynik, Okreg, Sondaze, ILE_SYMULACJI, WynikDemo, Histogram } from "./typy";
import { okregi, sondaze, KOLOR_PARTII } from "./dane";
import {
  NaDemo,
  PodzialMandatowKraj,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  RozkladPrawdopodobienstwaWOkregu,
} from "./ordynacja";
import {
  odmianaSlowaMandat,
  rysujHistogram,
  rysujWynikWyborow,
} from "./wykresy";
import { komentarze } from "./komentarz";

const wynikiWgSondazy: Sondaze = {};
Object.entries(sondaze).forEach(
  ([sondaz, wynik]) =>
    (wynikiWgSondazy[sondaz] = PodzialMandatowKraj(wynik as Wynik))
);

const sredniaSondazy: Wynik = {};
Object.entries(sondaze).forEach(([sondaz, wynik]) => {
  Object.entries(wynik).forEach(([partia, procent]) =>
    sredniaSondazy[partia]
      ? (sredniaSondazy[partia] += procent)
      : (sredniaSondazy[partia] = procent)
  );
});
const sumaProcentow =
  Object.values(sredniaSondazy).reduce((acc, curr) => (acc += curr), 0) /
  Object.entries(sondaze).length;
Object.entries(sredniaSondazy).forEach(([partia, procent]) => {
  sredniaSondazy[partia] =
    (sredniaSondazy[partia] / Object.entries(sondaze).length) * (100 / sumaProcentow);
});

const podzialWOkregach: Sondaze = {};
const podzialWOkregachDemo: Sondaze = {};
okregi.forEach((okreg) => {
  const wynikWOkregu = PoparcieWOkregu(sredniaSondazy, okreg);
  podzialWOkregach[okreg.miasto] = PodzialMandatowWOkregu(wynikWOkregu, okreg);
  podzialWOkregachDemo[okreg.miasto] = NaDemo(
    PodzialMandatowWOkregu(wynikWOkregu, okreg)
  );
});

export function RysujRozkladyWOkregu(wynik: Wynik, okreg: Okreg) {
  // console.log(wynik, okreg);
  const { rozklad: rozkladDemo } = RozkladPrawdopodobienstwaWOkregu(
    sredniaSondazy as Wynik,
    okreg,
    {
      laczOpozycje: true,
    }
  );
  const wykresRozkladuDemo: Sondaze = {};
  rozkladDemo.slice(0, 3).forEach(({ key, value }) => {
    wykresRozkladuDemo[(value / (ILE_SYMULACJI / 100)).toString() + "%"] =
      JSON.parse(key) as Wynik;
  });

  const { rozklad, histogram, wynikWOkregu } = RozkladPrawdopodobienstwaWOkregu(
    sredniaSondazy as Wynik,
    okreg,
    { laczOpozycje: false }
  );
  const wykresRozkladu: Sondaze = {};
  rozklad.slice(0, 6).forEach(({ key, value }) => {
    wykresRozkladu[(value / (ILE_SYMULACJI / 100)).toString() + "%"] =
      JSON.parse(key) as Wynik;
  });

  const header = document.createElement("h2");
  header.innerHTML = okreg.miasto;
  header.setAttribute("id", okreg.miasto);
  document.getElementById("analizy").appendChild(header);

  if (komentarze[okreg.miasto].msg) {
    const komentarz = document.createElement("p");
    komentarz.innerHTML = komentarze[okreg.miasto].msg;
    document.getElementById("analizy").appendChild(komentarz);
  }

  const kontener = document.createElement("div");
  const wykres = document.createElement("canvas");
  const wykresDemo = document.createElement("canvas");
  const naglowek = document.createElement("h3");

  kontener.setAttribute(
    "style",
    "width: 35%; margin-left: 10%; display: flex;"
  );

  naglowek.innerHTML = "Prawdopodobiestwo podziau mandatw";
  document.getElementById("analizy").appendChild(naglowek);

  wykres.setAttribute("id", "Miasto" + okreg.miasto);
  wykresDemo.setAttribute("id", "Miasto" + okreg.miasto + "Demo");

  kontener.appendChild(wykres);
  kontener.appendChild(wykresDemo);
  document.getElementById("analizy").appendChild(kontener);
  const tekst = document.createElement("p");
  tekst.setAttribute("style", "margin-bottom: 50px; ");
  tekst.innerHTML = interpretacjaWykresuRozkladuDemo(
    wykresRozkladuDemo,
    okreg
  );
  document.getElementById("analizy").appendChild(tekst);
  const tekstHistogramy = document.createElement("p");
  tekstHistogramy.setAttribute("style", "margin-bottom: 15px; ");
  tekstHistogramy.innerHTML = 'Wykresy poniej pokazuj prawdopodobiestwo poparcia danej partii. Im wyszy supek, tym wiksza szansa, e partia dostanie okrelony procent gosw.'
  document.getElementById("analizy").appendChild(tekstHistogramy);
  rysujHistogramy(okreg, histogram, wynikWOkregu);

  rysujWynikWyborow(wykresRozkladuDemo, "Miasto" + okreg.miasto, {
    poziomo: true,
    laczOpozycje: true,
  });
  rysujWynikWyborow(wykresRozkladu, "Miasto" + okreg.miasto + "Demo", {
    poziomo: true,
    laczOpozycje: false,
  });
}

window.onload = function() {
  let nrOkregu = 1;
  okregi.forEach((okreg) => {
    const text = document.createElement("div");
    text.innerHTML =
      `<h2>${okreg.miasto} (${nrOkregu})</h2>` +
      `<p>${komentarze[okreg.miasto].msg}</p>` +
      `<p>kliknij w mapk aby zobaczy szczegy</p>` +
      `<p> </p>`;
    text.id = `${okreg.miasto}Info`;
    text.className = "info";
    const graph = document.createElement("div");
    graph.setAttribute("id", "InfoMiasto" + okreg.miasto);
    text.appendChild(graph);
    const graph2 = document.createElement("div");
    graph2.setAttribute("id", "InfoMiasto2" + okreg.miasto);
    text.appendChild(graph2);
    document.getElementById("Info").appendChild(text);
    const icon = document.getElementById(okreg.miasto + 'Icon');
    icon.addEventListener('mouseover', () => {
      text.style.display = 'block';
    });
    icon.addEventListener("mouseout", () => {
      text.style.display = "none";
    });
    nrOkregu++;
    const rekomendacje = komentarze[okreg.miasto].komitet;
    if (rekomendacje.length) {
      if (rekomendacje.length > 1) {
        const aElement = document.getElementById(okreg.miasto + "Icon");        
        const path = aElement.getElementsByTagName('path')[0];
        path.setAttribute(
          "fill",
          `url(#stripes${rekomendacje[0].toLocaleLowerCase()}${rekomendacje[1].toLocaleLowerCase()})`
        );
        
      } else {
        document.getElementById(okreg.miasto + "Icon").style.fill =
          KOLOR_PARTII[komentarze[okreg.miasto].komitet[0]];
      }
    }
  });
  rysujWynikWyborow(sondaze, "sondaze", { laczOpozycje: false });
  rysujWynikWyborow(wynikiWgSondazy, "mandatyKraj", {
    poziomo: true,
    laczOpozycje: false,
    calySejm: true,
  });
  setTimeout(function () {
    rysujWynikWyborow(podzialWOkregach, "mandatyWOkregach", {
      poziomo: true,
      laczOpozycje: false,
    });
    rysujWynikWyborow(podzialWOkregachDemo, "mandatyWOkregachDemo", {
      poziomo: true,
      laczOpozycje: true,
    });
  }, 200);
  for (const okreg of okregi) {
    let i = 0;
    setTimeout(function () {
      RysujRozkladyWOkregu(sredniaSondazy, okreg);
    }, ++i * 500);
  }
};


function rysujHistogramy(okreg: Okreg, histogram: Histogram, wynikWOkregu: Wynik) {
  const histogramKontener = document.createElement("div");
  histogramKontener.setAttribute("style", "display: flex;");
  document.getElementById("analizy").appendChild(histogramKontener);
  Object.entries(sredniaSondazy).forEach(([partia, procent]) => {
    if (["PIS", "KONF"].includes(partia)) {
      return;
    }
    const elementName = okreg.miasto + "Histogram" + partia;
    const wykresHistogram = document.createElement("div");
    wykresHistogram.setAttribute("id", elementName);
    wykresHistogram.setAttribute("style", "width: 33%;");
    histogramKontener.appendChild(wykresHistogram);
    rysujHistogram(
      histogram[partia],
      elementName,
      KOLOR_PARTII[partia],
      partia,
      (Math.round(wynikWOkregu[partia] * 5) / 5).toString()
    );
    // Rysuj kopi obok mapy
    if (partia === komentarze[okreg.miasto].komitet[0]) {
      rysujHistogram(
        histogram[partia],
        "InfoMiasto" + okreg.miasto,
        KOLOR_PARTII[partia],
        partia,
        (Math.round(wynikWOkregu[partia] * 5) / 5).toString()
      );
    }
    if (komentarze[okreg.miasto].komitet.length > 1 &&
      partia === komentarze[okreg.miasto].komitet[1]) {
      rysujHistogram(
        histogram[partia],
        "InfoMiasto2" + okreg.miasto,
        KOLOR_PARTII[partia],
        partia,
        (Math.round(wynikWOkregu[partia] * 5) / 5).toString()
      );
    }
  });
}

function interpretacjaWykresuRozkladuDemo(wykresRozkladuDemo: Sondaze, okreg: Okreg):string {
  const podstawowyScenariusz = Object.entries(wykresRozkladuDemo)[0];
  const drugiScenariusz = Object.entries(wykresRozkladuDemo)[1];
  return (
    `W okrgu ${okreg.miasto} z prawdopodobiestwem ${podstawowyScenariusz[0]} ` +
    `demokratyczna opozycja zdobdzie ${
      podstawowyScenariusz[1].DEMO
    } ${odmianaSlowaMandat(podstawowyScenariusz[1].DEMO)} ` +
    `a PiS i Konfederacja zdobd ${
      podstawowyScenariusz[1].NIEDEMO
    } ${odmianaSlowaMandat(podstawowyScenariusz[1].NIEDEMO)}. ` +
    (drugiScenariusz[1].DEMO > podstawowyScenariusz[1].DEMO
      ? `Jest ${drugiScenariusz[0]} szans, e `
      : `Istnieje ${drugiScenariusz[0]} ryzyko, e `) +
    `demokratyczna opozycja zdobdzie ${
      drugiScenariusz[1].DEMO
    } ${odmianaSlowaMandat(drugiScenariusz[1].DEMO)} ` +
    `a PiS i Konfederacja zdobd ${
      drugiScenariusz[1].NIEDEMO
    } ${odmianaSlowaMandat(drugiScenariusz[1].NIEDEMO)}.`
  );
}

