import { okregi, wybory2019 } from "./dane";
import {
  Histogram,
  HistogramPartiiInternal,
  ILE_SYMULACJI,
  Okreg,
  PoparciePartii,
  Wynik,
  WynikDemo,
} from "./typy";

import random from "random";
import { sortObjectByValues, sortedStringify } from "./util";

// Odchylenie standardowe w pkt. proc. zgodnie z ktorym losujemy wyniki
const STDEV = 3;

// Do jakich wartoci rysowa wykres
const ZAKRES = STDEV * 2.5

export function NaDemo(wynik: Wynik): WynikDemo {
  return {
    DEMO: wynik.KO + (wynik.LEWICA || 0) + (wynik.TD || 0),
    NIEDEMO: wynik.PIS + (wynik.KONF || 0),
  };
}

let glosyTD = 0;
let glosyMnoznikTD = 0;

export function PoparcieWOkregu(wynik: Wynik, okreg: Okreg): Wynik {
  const wynikWOkregu: Wynik = {};
  let sumaProcentowWOkregu = 0;
  for (const [partia, procent] of Object.entries(wynik)) {
    let mnoznik = 1;
    if (partia === "TD") {
      mnoznik = ((okreg.wynik2019.PSL! / wybory2019.PSL!) + (okreg.wynik2019.KO! / wybory2019.KO!)) / 2;
      wynikWOkregu.TD = wynik.TD! * mnoznik;
      glosyTD += wynik.TD! * okreg.glosy2019 / 100;
      glosyMnoznikTD += wynik.TD! * mnoznik * okreg.glosy2019 / 100;
    } else {
        mnoznik =
          wynik[partia as keyof Wynik]! / wybory2019[partia as keyof Wynik]!;
      wynikWOkregu[partia as keyof Wynik] =
        (okreg.wynik2019[partia as keyof Wynik] ||
          wynik[partia as keyof Wynik]!) * mnoznik;
    }
    sumaProcentowWOkregu += wynikWOkregu[partia as keyof Wynik];
  }
  return wynikWOkregu;
}

export function PodzialMandatowKraj(wynik: Wynik) {
  glosyTD = 0;
  glosyMnoznikTD = 0;
  const mandatyWKraju: Wynik = {
    PIS: 0,
    KO: 0,
    LEWICA: 0,
    TD: 0,
    KONF: 0,
  };
  for (const okreg of okregi) {
    const wynikWOkregu = PoparcieWOkregu(wynik, okreg);
    const mandatyWOkregu = PodzialMandatowWOkregu(wynikWOkregu, okreg);
    for (const [partia, mandaty] of Object.entries(mandatyWOkregu)) {
      mandatyWKraju[partia as keyof Wynik] += mandaty;
    }
  }
  return mandatyWKraju;
}

export function PodzialMandatowWOkregu(
  wynik: Wynik,
  okreg: Okreg,
  rozkladRoznic?: any[]
): Wynik {
  const dhont: any[] = [];
  for (const [partia, procent] of Object.entries(wynik)) {
    for (let i = 1; i <= 10; i++) {
      dhont.push({ partia: partia, iloraz: procent / i });
    }
  }
  const posortowanyDhont = dhont.sort((a, b) => {
    return b.iloraz - a.iloraz;
  });
  const roznica =
    ((posortowanyDhont[okreg.mandaty - 1].iloraz -
      posortowanyDhont[okreg.mandaty].iloraz) *
      okreg.glosy2019) /
    100;
  rozkladRoznic?.push(Math.round(roznica));
  const podzial = posortowanyDhont
    .slice(0, okreg.mandaty)
    .map((x) => x.partia)
    .reduce((acc, cur) => {
      acc[cur] ? acc[cur]++ : (acc[cur] = 1);
      return acc;
    }, {});

  return podzial;
}

export function RozkladPrawdopodobienstwaWOkregu(
  wynik: Wynik,
  okreg: Okreg
) {
  const wynikWOkregu = PoparcieWOkregu(wynik, okreg);
  const rozklad = {};
  const rozkladDemo = {};
  const rozkladRoznic = [];
  interface HistogramInternal {
    [partia: string]: HistogramPartiiInternal;
  }
  const histogram: HistogramInternal = {};

  for (let i = 1; i <= ILE_SYMULACJI; i++) {
    // Losowanie poparcia dla partii
    const wynikWOkreguRandom: Wynik = {};
    let suma = 0;
    for (const [partia, procent] of Object.entries(wynikWOkregu)) {
      const wynikLosowyUncapped = random.normal(procent, STDEV)();
      const wynikLosowy =
        Math.round(
          Math.max(Math.min(wynikLosowyUncapped, procent + ZAKRES), procent - ZAKRES) * 5
        ) / 5;
      wynikWOkreguRandom[partia as keyof Wynik] = wynikLosowy;
      if (!histogram[partia]) {
        histogram[partia] = {};
      }
      suma += wynikLosowy
      const wynikLosowyKey = String(wynikLosowy);
      histogram[partia][wynikLosowyKey]
        ? histogram[partia][wynikLosowyKey].czestosc++
        : (histogram[partia][wynikLosowyKey] = { czestosc: 1 });
    }
    // Podzia mandatw
    const mandatyWOkregu = PodzialMandatowWOkregu(
      wynikWOkreguRandom,
      okreg,
      rozkladRoznic
    );
    const str = sortedStringify(mandatyWOkregu);
    const strDemo = sortedStringify(NaDemo(mandatyWOkregu))
    // console.log(str, strDemo);
    rozklad[str] ? rozklad[str]++ : (rozklad[str] = 1);
    rozkladDemo[strDemo] ? rozkladDemo[strDemo]++ : (rozklad[strDemo] = 1);
    // Zapisanie mandatw w histogramie
    for (const [partia, procent] of Object.entries(wynikWOkreguRandom)) {
      // Sumujemy ilo mandatw dla danego procentu ze wszystkich symulacji.
      histogram[partia][String(procent)].mandaty
        ? (histogram[partia][String(procent)].mandaty +=
            mandatyWOkregu[partia] || 0)
        : (histogram[partia][String(procent)].mandaty =
            mandatyWOkregu[partia] || 0);
    }
  }
  const histogramSorted: Histogram = {};
  for (const [partia, procent] of Object.entries(histogram)) {
    histogramSorted[partia] = transformHistogram(histogram[partia]);
  }
  return {
    rozklad: sortObjectByValues(rozklad),
    rozkladDemo: sortObjectByValues(rozkladDemo),
    histogram: histogramSorted,
    wynikWOkregu,
  };
}

function transformHistogram(
  obj: HistogramPartiiInternal
): PoparciePartii[] {
  // Convert the object to an array of key-value pairs
  const keyValueArray = Object.entries(obj);

  // Sort the array based on the values (ascending order)
  return keyValueArray
    .sort((a, b) => Number.parseFloat(b[0]) - Number.parseFloat(a[0]))
    .map(([key, value]) => {
      return { procent: key, czestosc: value.czestosc, mandaty: Math.round(value.mandaty / value.czestosc) };
    });
}

