import { KOLOR_PARTII } from "./dane";
import { Histogram, ILE_SYMULACJI, PoparciePartii, Sondaze } from "./typy";
import Chart, { ChartItem } from "chart.js/auto";
import Color from 'color';

export function rysujWynikWyborow(wynikWyborow: Sondaze, targetElement: string, options: { poziomo?: boolean, laczOpozycje?: boolean, calySejm?: boolean } = {}) {
    new Chart(document.getElementById(targetElement) as ChartItem, {
      type: "bar",
      data: {
        labels: Object.entries(wynikWyborow).map(([sondaz, wynik]) =>
          sondaz.toString()
        ),
        datasets: !options.laczOpozycje
          ? [
              {
                label: "Lewica",
                data: Object.values(wynikWyborow).map((wynik) => wynik.LEWICA),
                backgroundColor: KOLOR_PARTII["LEWICA"],
              },
              {
                label: "KO",
                data: Object.values(wynikWyborow).map((wynik) => wynik.KO),
                backgroundColor: KOLOR_PARTII["KO"],
              },
              {
                label: "Trzecia Droga",
                data: Object.values(wynikWyborow).map((wynik) => wynik.TD),
                backgroundColor: KOLOR_PARTII["TD"],
              },
              {
                label: "Konfederacja",
                data: Object.values(wynikWyborow).map((wynik) => wynik.KONF),
                backgroundColor: KOLOR_PARTII["KONF"],
              },
              {
                label: "PiS",
                data: Object.values(wynikWyborow).map((wynik) => wynik.PIS),
                backgroundColor: KOLOR_PARTII["PIS"],
              },
            ]
          : [
              {
                label: "Opozycja Demokratyczna",
                data: Object.values(wynikWyborow).map((wynik) => wynik.DEMO),
                backgroundColor: "#E56701",
              },
              {
                label: "PiS + Konfederacja",
                data: Object.values(wynikWyborow).map((wynik) => wynik.NIEDEMO),
                backgroundColor: "rgb(38, 55, 120)",
              },
            ],
      },
      options: options?.poziomo
        ? {
            indexAxis: "y",
            scales: {
              x: {
                stacked: true,
                max: options?.calySejm ? 460 : null,
                afterBuildTicks: options?.calySejm ? (axis) =>
                  (axis.ticks = [0, 230, 460].map((v) => ({ value: v }))) : null,
              },
              y: {
                stacked: true,
              },
            },
          }
        : {},
    });
}

export function odmianaSlowaMandat(mandaty: number) {
  return [0, 5, 6, 7, 8, 9, 10].includes(mandaty)
              ? "mandatw"
              : mandaty > 1
              ? "mandaty"
              : "mandat"
}

export function rysujHistogram(
  histogram: PoparciePartii[],
  targetElement: string,
  color: string = "rgb(150, 195, 110)",
  nazwa: string,
  procentZSondazy: string
) {
  const canvas = document.createElement("canvas");
  document.getElementById(targetElement).appendChild(canvas);
  const oczekiwaneMandaty = histogram.filter(
    (value) => value.procent === String(procentZSondazy)
  )[0].mandaty;

  const histogramPlus = histogram.map((poparcie) => {return { ...poparcie, czestosc: poparcie.mandaty > oczekiwaneMandaty ? poparcie.czestosc : 0}});
  const histogramMinus = histogram.map((poparcie) => {
    return {
      ...poparcie,
      czestosc: (poparcie.mandaty < oczekiwaneMandaty && Number(poparcie.procent) > 0) ? poparcie.czestosc : 0,
    };
  });
  const histogramZero = histogram.map((poparcie) => {
    return {
      ...poparcie,
      czestosc: (poparcie.mandaty === oczekiwaneMandaty && Number(poparcie.procent) > 0) ? poparcie.czestosc : 0,
    };
  });
  new Chart(canvas as ChartItem, {
    type: "bar",
    data: {
      labels: histogram.map(
        ({ procent, czestosc, mandaty }) =>
          `${procent}% -> ${mandaty} ${odmianaSlowaMandat(mandaty)}`
      ),
      datasets: [
        {
          label:
            "" +
            (oczekiwaneMandaty + 1) +
            " " +
            odmianaSlowaMandat(oczekiwaneMandaty + 1),
          data: histogramPlus.map(({ procent, czestosc }) =>
            czestosc
          ),
          backgroundColor: color,
        },
        {
          label:
            "" +
            oczekiwaneMandaty +
            " " +
            odmianaSlowaMandat(oczekiwaneMandaty),
          data: histogramZero.map(({ procent, czestosc }) =>
            czestosc
          ),
          backgroundColor: Color(color).fade(0.35).toString(),
        },
      ].concat(
        oczekiwaneMandaty > 0
          ? [
              {
                label:
                  "" +
                  (oczekiwaneMandaty - 1) +
                  " " +
                  odmianaSlowaMandat(oczekiwaneMandaty - 1),
                data: histogramMinus.map(({ procent, czestosc }) => czestosc),
                backgroundColor: Color(color).fade(0.7).toString(),
              },
            ]
          : [{ label: "", data: [], backgroundColor: "white" }]
      ),
    },
    options: {
      plugins: {
        legend: {
          title: {
            display: true,
            text: 'Przewidywany wynik w okrgu dla ' + nazwa + ': ' + procentZSondazy + '%',
            color: 'black',
            font: {
              size: 15,
            }
          },
        },
      },
      scales: {
        x: {
          ticks: {
            callback: function (value, index, ticks) {
              return this.getLabelForValue(index).split(" ")[0];
            },
          },
          stacked: true,
        },
        y: {
          max: (ILE_SYMULACJI / 10000) * 410,
          display: false,
          stacked: true,
        },
      },
    },
  });
  const procentyOczekiwanych = histogram.filter(
    (value) => value.mandaty === oczekiwaneMandaty
  );
  const minProcentDoOczekiwanych = procentyOczekiwanych.reduce((acc, curr) =>
    extractProcent(curr) < extractProcent(acc) ? curr : acc
  );
  const maxProcentDoOczekiwanych = procentyOczekiwanych.reduce((acc, curr) =>
    extractProcent(curr) > extractProcent(acc) ? curr : acc
  );
  const tekst = document.createElement("p");
  tekst.setAttribute("style", "margin-bottom: 100px; ");
  tekst.innerHTML = `Jeli ${nazwa} otrzyma od ${
    Math.max(extractProcent(minProcentDoOczekiwanych), 0)
  }% do ${
    maxProcentDoOczekiwanych.procent
  }% gosw, to zdobdzie ${oczekiwaneMandaty} ${odmianaSlowaMandat(
    oczekiwaneMandaty
  )}.`;
  document.getElementById(targetElement).appendChild(tekst);
}

function extractProcent(poparcie: PoparciePartii) {
  return Number(poparcie.procent);
}